<script>
	import Clipboard from 'clipboard'
	import https from '../api/https'
	export default {
		name: 'Settings',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,

                teamData: {
                    share: {
                        url: '',
                        code: '',
                    }
                },
                sysType: 'Android',
				FacebookUrl: '',
				WhatsAppUrl: '',
				SMSUrl: '',
				EmailUrl: '',
			}
		},
		created(){
			this.getData()
            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }

            let u = navigator.userAgent
			let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
			let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
			if (isAndroid) {
				this.sysType = 'Android'
			}else if (isIOS) {
				this.sysType = 'IOS'
			}
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			this.bgWrapHeight = viwePortHeight
		},
		methods: {
			getData: function(){
				this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/user/team' ).then((data) => {
					this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.teamData = res.data
                        
                        if(this.teamData.share && this.teamData.share.url) {
                            let str1 = 'This is a great movie clip.'
                            let resultUrl = ''
                            
                            if(this.sysType == 'Android') {
                                resultUrl = 'sms: '+'?body='+str1+this.teamData.share.url
                            }else if (this.sysType == 'IOS') {
                                resultUrl = 'sms: '+'&body='+str1+this.teamData.share.url
                            }else {
                                resultUrl = 'sms: '+'?body='+str1+this.teamData.share.url
                            }
                            
                            this.FacebookUrl = 'fb://friends'
                            // this.WhatsAppUrl = "https://wa.me/?text=" + str1 +this.teamData.share.url
                            this.WhatsAppUrl = "whatsapp://send?text=" + str1 +this.teamData.share.url
                            
                            this.SMSUrl = resultUrl
                            this.EmailUrl = 'mailto:?subject=&body='+str1+this.teamData.share.url
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
					console.log(data)
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            copyText(type){
				let that = this
				// this.toastText.close();
                var clipboard = new Clipboard('.copyBtn');
                clipboard.on('success', function() {
					that.$toast({
						message: type+' Copied',
						forbidClick: true,
						duration: 2000,
						className: 'smallToast'
					});
                });

                clipboard.on('error', function() {
					that.$toast({
						message: 'Copy failed',
						forbidClick: true,
						duration: 2000,
						className: 'smallToast'
					});
                });
			},
            shareCopy() {
                var clipboard = new Clipboard('.copyBtn');
                clipboard.on('success', function() {
					// 
                });
                clipboard.on('error', function() {
					// 
                });
			},
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
